import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

// –°–µ–ª–µ–∫—Ç–æ—Ä–∏ DOM
const galleryContainer = document.querySelector(".gallery"); // –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–∞—Ä—Ç–æ–∫ –≥–∞–ª–µ—Ä–µ—ó
const loader = document.querySelector(".loader");           // –Ü–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è
const loadMoreBtn = document.querySelector(".load-more");   // –ö–Ω–æ–ø–∫–∞ "Load More"

// –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è SimpleLightbox
const lightbox = new SimpleLightbox(".gallery a", {
  captionsData: "alt", // –ë–µ—Ä–µ–º–æ –ø—ñ–¥–ø–∏—Å –∑ –∞—Ç—Ä–∏–±—É—Ç–∞ alt –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è
  captionDelay: 250,   // –ó–∞—Ç—Ä–∏–º–∫–∞ –ø–µ—Ä–µ–¥ –ø–æ—è–≤–æ—é –ø—ñ–¥–ø–∏—Å—É
});

/**
 * –î–æ–¥–∞—î –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è —É –≥–∞–ª–µ—Ä–µ—é
 * @param {Array} images - –ú–∞—Å–∏–≤ –æ–±'—î–∫—Ç—ñ–≤ –∑–æ–±—Ä–∞–∂–µ–Ω—å
 */
export function createGallery(images) {
  // –ì–µ–Ω–µ—Ä—É—î–º–æ HTML –¥–ª—è –∫–æ–∂–Ω–æ—ó –∫–∞—Ä—Ç–∏–Ω–∫–∏
  const markup = images
    .map(
      (img) => `
    <a href="${img.largeImageURL}" class="gallery-item">
      <div class="photo-card">
        <img class="photo-card-img" src="${img.webformatURL}" alt="${img.tags}" loading="lazy" />
        <ul class="info">
          <li><b>Likes:</b> ${img.likes}</li>
          <li><b>Views:</b> ${img.views}</li>
          <li><b>Comments:</b> ${img.comments}</li>
          <li><b>Downloads:</b> ${img.downloads}</li>
        </ul>
      </div>
    </a>
  `
    )
    .join(""); // –û–± º—î–¥–Ω—É—î–º–æ –≤—Å—ñ –∫–∞—Ä—Ç–∫–∏ –≤ –æ–¥–∏–Ω —Ä—è–¥–æ–∫

  // –í—Å—Ç–∞–≤–ª—è—î–º–æ –∑–≥–µ–Ω–µ—Ä–æ–≤–∞–Ω—É —Ä–æ–∑–º—ñ—Ç–∫—É —É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –≥–∞–ª–µ—Ä–µ—ó
  galleryContainer.insertAdjacentHTML("beforeend", markup);

  // –û–Ω–æ–≤–ª—é—î–º–æ SimpleLightbox –ø—ñ—Å–ª—è –¥–æ–¥–∞–≤–∞–Ω–Ω—è –Ω–æ–≤–∏—Ö –µ–ª–µ–º–µ–Ω—Ç—ñ–≤
  lightbox.refresh();
}

/**
 * –û—á–∏—â–∞—î –≥–∞–ª–µ—Ä–µ—é (–≤–∏–¥–∞–ª—è—î –≤—Å—ñ –µ–ª–µ–º–µ–Ω—Ç–∏)
 */
export function clearGallery() {
  galleryContainer.innerHTML = "";
}

/**
 * –ü–æ–∫–∞–∑—É—î —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è
 */
export function showLoader() {
  loader.classList.add("visible");
}

/**
 * –•–æ–≤–∞—î —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è
 */
export function hideLoader() {
  loader.classList.remove("visible");
}

/**
 * –ü–æ–∫–∞–∑—É—î –∫–Ω–æ–ø–∫—É "Load More"
 */
export function showLoadMoreButton() {
  loadMoreBtn.classList.remove("hidden");
}

/**
 * –•–æ–≤–∞—î –∫–Ω–æ–ø–∫—É "Load More"
 */
export function hideLoadMoreButton() {
  loadMoreBtn.classList.add("hidden");
}
